import React, { Component,Suspense } from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  FormGroup,
  Container,
  Label,
  Row,
} from 'reactstrap';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import {Combobox} from 'react-widgets';
import 'react-widgets/dist/css/react-widgets.css';
import Grid from '@material-ui/core/Grid';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import axios from 'axios';
import { saveAs } from 'file-saver';
import Image2 from '../assets/img/brand/loading2.gif'

class Invoice extends Component {
  constructor() {
    super()
    

    this.state = {

      companies:[],company:[],deliveries:[],
      invoices:[],
      startDate: new Date(),
      endDate: new Date(),
      deliTotal:0,companyPayments:[],debitAmount:0,
      showAni:false,
      myElement : ( 
        this.MuiTable
       )
    
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount(){
    this.getCompanies();
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }
  async onSubmit(e){
    e.preventDefault();
  }

  getCompanies(){
    fetch('/getcompanies')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        companies : json
      });
    });
  }
  getDeliveriesInvoice=async ()=>{
    if(this.state.company.length==0){
      alert('Please Choose A Company');
    }
    else{
    var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
    var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
    await axios.post('/getdeliveriesbyrange',{
      startDate: stDate,
      endDate: enDate,
      companyId: this.state.company.companyId
    }).then(res => {
      this.setState({
        deliveries:res.data
      })
    })
  }
  var total=0;
  if(this.state.deliveries.length > 0){
    
    for(var i=0 ; i<this.state.deliveries.length; i++){
      total+= this.state.deliveries[i].vehicleCapacity * this.state.deliveries[i].unloadingPlaceIncomeRate ;
    }
    this.setState({
      deliTotal:total
    })
  }
  else{
    this.setState({
      deliTotal:0,
    })
  }

  }
  //add a new invoice
  addInvoice= async()=>{
    var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
    var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
    var range = stDate.toString()+ ' To ' + enDate.toString();
    try{
      await axios.post("/addInvoice",{
        company:this.state.company.companyId,
        range:range,
        amount:this.state.deliTotal
      }).then(res=>{
        var status=res.data.status;
        if(!status.localeCompare('Successfull')){
          alert('Successfully Added');
          this.getInvoices();
          this.onIncomeAdd();
          this.onGetInvoiceRp();
        }
        else{
          alert('Bad Credentials')
        }
     
      })

      }catch(e){
        console.log(e);
        alert('Failed to Add')
      }
  }
  //get invoices
  getInvoices(){
    fetch('/getinvoice')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        invoices : json
      });
    });
  }
      // update company payments table
      onIncomeAdd= async()=>{
        await axios.post('/getpay',{
          company : this.state.company.companyId
        })  .then(res =>{
          this.setState({ 
            companyPayments : res.data,
            
          });
        });

        await axios.post('/updatepay',{
          company : this.state.company.companyId,
          credit : this.state.companyPayments[0].companyPaymentsCredit,
          debit : this.state.companyPayments[0].companyPaymentsDebit + this.state.deliTotal,
        }).then(res =>{
          var status=res.data.status;
          if(!status.localeCompare('UnSuccessfull')){
            alert('Error please contact developer');
          }
        })
    }
  //get company incomes
 /* getCompanyPayments=async()=>{
    await axios.post('http://localhost:4000/getpay',{
      company : this.state.company.companyId
    })  .then(res =>{
      this.setState({ 
        companyPayments : res.data,
        
      });
    });
}*/
  // when deleting a invoice
  onIncomeRemove= async()=>{
    try{
    await axios.post('/getpay',{
      company : this.state.company.companyId,
    })  .then(res =>{
      this.setState({ 
        companyPayments : res.data,
        
      });
    })
  }catch(e){
    console.log(e)
  }
    try{
    await axios.post('/updatepay',{
      company : this.state.company.companyId,
      credit : this.state.companyPayments[0].companyPaymentsCredit ,
      debit : this.state.companyPayments[0].companyPaymentsDebit - this.state.debitAmount
    }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('UnSuccessfull')){
        alert('Error please contact developer');
      }
    })
  }catch(e){
    console.log(e)
  }
}
  handleStartDateChange = date =>{
    this.setState({
      startDate:date
    },()=>{
      this.getDeliveriesInvoice();
      this.getInvoices();
    })
  };
  handleEndDateChange = date =>{
    this.setState({
      endDate:date
    },()=>{
      this.getDeliveriesInvoice();
      this.getInvoices();
    })
  };
  dateFormater(cell , row){
    var stDate = new Date(cell);
    var date = stDate.getFullYear() + '-' + (stDate.getMonth() + 1) + '-' + stDate.getDate();
    return `${date}`
  }
  //handlers for table
  onAfterDeleteRow= async (rowKeys,cell)=> {
    this.setState({
      debitAmount:cell[0].invoiceAmount
    })
    await axios.post('/deleteinvoice',{
      invoiceId : rowKeys,
    }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('Successfull')){
        alert('Deleted');
        this.onIncomeRemove();
        this.getInvoices();
      }
      else{
        alert ('Failed To Delete');
      }
    })
  }
  
  selectRowProp={
    mode:'radio',
    onSelect: this.onRowSelect,
    bgColor:'yellow'
  }
  
  options = {
    afterDeleteRow: this.onAfterDeleteRow  // A hook for after droping rows.
  
  };
  onGetInvoiceRp= async()=>{
    this.setState({
      showAni : true
    })
    var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
    var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
    var range = stDate.toString()+ ' To ' + enDate.toString();
     await axios.post(`/getinvoiceRp`, {
       company : this.state.company.companyName,
      invoice : this.state.deliveries,
      range: range,
      deliTotal:this.state.deliTotal
       })
       .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
       .then((res) => {
         const pdfBlob = new Blob([res.data], { type: 'application/pdf' });
  
         saveAs(pdfBlob, `Invoice -${this.state.company.companyName}.pdf`);
       })
       this.setState({
        showAni : false
      })
  }
  loading = () => <div className="animated fadeIn pt-1 text-center"><img src={Image2} alt="loading..." /></div>

  render() {
    
    var companies=this.state.companies;
    return (
      <div className="animated fadeIn">
        <Container>
        <Row>
          <Col  xs="12" ys="">
          <Card responsive bordered>
              <CardHeader>
              <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="company">Company</Label>
                    </Col>
                    <Col xs="5" md="3">
                    <div>
                      <Combobox
                        data={this.state.companies}
                        suggest={true}
                        //valueField='companyName'
                        textField='companyName'
                        caseSensitive={false}
                        minLength={2}
                        filter='contains'
                        onChange={(value)=>{
                          this.setState({
                            company:value
                          },()=>{
                            this.getDeliveriesInvoice();
                            this.getInvoices();
                          });
                        }
                        }
                      />
                  </div>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                 
                 <MuiPickersUtilsProvider utils={DateFnsUtils}>
                   <Grid container justify="space-around">
                     <Col >
                     <KeyboardDatePicker
                       margin="normal"
                       id="date-picker-dialog"
                       label="Start Date"
                       format="yyyy-MM-dd"
                       value={this.state.startDate}
                       onChange={this.handleStartDateChange}
                       KeyboardButtonProps={{
                       'aria-label': 'change date',
                       }}
                     />
                     </Col>
                     <Col xs="2" md="3">
                     <KeyboardDatePicker
                       margin="normal"
                       id="date-picker-dialog"
                       label="End Date"
                       format="yyyy-MM-dd"
                       value={this.state.endDate}
                       onChange={this.handleEndDateChange}
                       KeyboardButtonProps={{
                       'aria-label': 'change date',
                       }}
                     />
                     </Col>
                     </Grid>
                   </MuiPickersUtilsProvider>
                   </FormGroup>
              </CardHeader>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
              <div>
                <BootstrapTable data={this.state.deliveries } height='375' scrollTop={'Bottom'} search={true}
                pagination
                tableStyle={ { border: '#0000FF 2.5px solid' } }
                bodyStyle={ { border: 'green 1px solid' }}>
     
                <TableHeaderColumn width="110" tdStyle={ { whiteSpace: 'normal' } } dataField='deliveryNoteNO' 
                      isKey>Delivery Note</TableHeaderColumn>
                <TableHeaderColumn width="110" tdStyle={ { whiteSpace: 'normal' } } dataField='deliveryDate'
                      dataFormat={this.dateFormater}>Date</TableHeaderColumn>
               <TableHeaderColumn width="150" tdStyle={ { whiteSpace: 'normal' } } dataField='vehicle_vehicleNumber'
                      >Lorry</TableHeaderColumn>
                <TableHeaderColumn width="200" tdStyle={ { whiteSpace: 'normal' } } dataField='uploadingPlaceAddress'
                      >Unloading Place</TableHeaderColumn>
                <TableHeaderColumn width="70" tdStyle={ { whiteSpace: 'normal' } } dataField='vehicleCapacity'
                      >Capacity<br/>(cubes)</TableHeaderColumn>
                <TableHeaderColumn width="150" tdStyle={ { whiteSpace: 'normal' } } dataField='unloadingPlaceIncomeRate'
                      >Rate</TableHeaderColumn>

                </BootstrapTable>
 
            </div>
              </CardBody>
              <CardFooter>
                <div className="animated fadeIn pt-1 text-center">
                <FormGroup row>
                  <Col >
                <Button onClick={()=>{this.addInvoice()}}>Create Invoice</Button>
                </Col>
                <Col >
                {
                  this.state.showAni?
                <div ><img src={Image2} alt="loading..." /></div> 
                  :null
                }
                </Col>
                <Col>
                <Button onClick={()=>{this.loading();this.onGetInvoiceRp()}}>Invoice Report</Button>
                </Col>
                </FormGroup>
                </div>
              </CardFooter>
            </Card>
    
          </Col>
        
        </Row>  
        </Container>
        <Container>
          <Row>
            <Card>
              <CardHeader>
                <Label>Invoice History</Label>
              </CardHeader>
              <CardBody>
                <FormGroup>
                  <BootstrapTable data={this.state.invoices} search={true} pagination
                  selectRow={this.selectRowProp} deleteRow={true} options={this.options}>
                      <TableHeaderColumn isKey dataField="invoiceId">No</TableHeaderColumn>
                      <TableHeaderColumn dataField="companyName">Company</TableHeaderColumn>
                      <TableHeaderColumn dataField="invoiceRange">Month</TableHeaderColumn>
                      <TableHeaderColumn dataField="invoiceAmount" tdStyle={{'text-align' : 'right'}}>Amount(Rs)</TableHeaderColumn>
                      <TableHeaderColumn dataField="invoiceCreated" dataFormat={this.dateFormater}>Created Date</TableHeaderColumn>
                  </BootstrapTable>
                </FormGroup>
              </CardBody>
            </Card>
          </Row>
        </Container>
       
        
      </div>
    );
  }
  
}

export default Invoice;
